export default {
  props: ['payShow', 'type'],
  data () {
    return {
      wx_account: '',
      name: '',
      zfb_phone: '',
      bank: '',
      sub_branch: '',
      bank_name: '',
      card: '',
      errShow: false
    }
  },
  methods: {
    sure: function (s) {
      if (s === 'wx') {
        if (!this.wx_account) {
          this.errShow = true
        } else {
          this.errShow = false
          this.$emit('getStyleInfo', this.wx_account)
          this.$emit('closePayInfo', false)
        }
      } else if (s === 'zfb') {
        if (!this.name || !this.zfb_phone) {
          this.errShow = true
        } else {
          this.errShow = false
          let obj = {}
          obj.name = this.name
          obj.zfb_phone = this.zfb_phone
          this.$emit('getStyleInfo', obj)
          this.$emit('closePayInfo', false)
        }
      } else if (s === 'yl') {
        if (!this.bank || !this.sub_branch || !this.bank_name || !this.card) {
          this.errShow = true
        } else {
          this.errShow = false
          let obj = {}
          obj.bank = this.bank
          obj.sub_branch = this.sub_branch
          obj.bank_name = this.bank_name
          obj.card = this.card
          this.$emit('getStyleInfo', obj)
          this.$emit('closePayInfo', false)
        }
      }
    },
    close: function () {
      this.$emit('closePayInfo', false)
    }
  }
}
